.title {
  text-align: center;
  margin-top: $navbar-height;
  h1 {
    color: $bronze; } }

.sub-cat-title {
  width: 50%;
  margin: 0 auto;
  padding: 1.2em 0 .4em 0em;
  font-size: 3rem;

  span {
    font-size: 1.4rem; } }

.price-table {
  width: 50%;
  font-size: 2rem;
  margin: 0 auto;
  padding-left: .4em;

  li {
    display: flex;
    justify-content: space-between;
    min-width: 30%;

    .text-info {
      color: $bronze; } } }
